const Sidebar = () => {
  return (
    <aside className="w-64 p-4 border-r border-gray-200 h-screen">
      <div className="mb-6">
        <h1 className="text-2xl font-bold">TEXTOK</h1>
      </div>
      <nav className="space-y-4">
        {['메인', '숏 피드', '롱 피드', '팔로우', '작성', '알림', '프로필'].map((item) => (
          <div key={item} className="cursor-pointer hover:text-green-600">
            {item}
          </div>
        ))}
      </nav>
      <div className="mt-6">
        <input type="text" placeholder="Search" className="w-full px-3 py-2 border rounded" />
        <button className="w-full mt-2 bg-green-500 text-white py-2 rounded">로그인</button>
      </div>
    </aside>
  );
};

export default Sidebar;
